.toolbar{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: lightblue;
    height: 56px;
}

.toolbar_navigation {
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
}

.toolbar_logo {
    margin-left: 0.5rem;
}

.toolbar_logo a {
    color: white;
    text-decoration: none;
    font-size: 1.5rem;
}

.spacer {
    flex: 1;
}

.toolbar_navigation-item ul{
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.toolbar_navigation-item li{
    padding: 0 0.5rem;
}

.toolbar_navigation-item a {
    color: white;
    text-decoration: none;

}

.toolbar_navigation-item a:hover,
.toolbar_navigation-item a:active {
    color: darkgreen;
}

